import $ from 'jquery';
import React from 'react';
import { Link } from 'react-router-dom';

export class DeleteAddress extends React.Component {
  componentDidMount(): void {
    $('.window-background').click(() => {
      $('.window-container').removeClass('visible');
    });
    $('.window-content-close').click(() => {
      $('.window-container').removeClass('visible');
    });
    $('.profile-address-delete_address-content-fields-cancel').click(() => {
      $('.window-container').removeClass('visible');
    });
  }
  render() {
    return (
      <React.Fragment>
        <div className="profile-address-delete_address-container window-container">
          <div className="profile-address-delete_address-background window-background">

          </div>
          <div className="profile-address-delete_address-content window-content">
            <div className="profile-address-delete_address-content-close window-content-close"></div>

            <div className="profile-address-delete_address-content-title">
              Удалить адрес?
            </div>
            <div className="profile-address-delete_address-content-fields">
              <div className="profile-address-delete_address-content-fields-target">
                <div className="profile-address-delete_address-content-fields-target-title">
                  г. Москва
                </div>
                <div className="profile-address-delete_address-content-fields-target-full_address">
                  Ул. Зои и Александра Космедемьянских д 2 строение 2, этаж 3, кваритра 334
                </div>
              </div>
              <div className="profile-address-delete_address-content-fields-cancel">
                Отменить
              </div>
              <div className="profile-address-delete_address-content-fields-submit">
                Удалить
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    )
  }
}