import React from 'react';
import { Link } from 'react-router-dom';
import { ProfileHeader } from '../ProfileHeader';
import { Footer } from '../Footer';

export class ProfilePassword extends React.Component {
  render() {
    return (
      <React.Fragment>
        <ProfileHeader />
        <div className='profile parentbox'>
          <div className="profile-container childbox">
            <div className="profile-menu">
              <Link to="/profile/status" className="profile-menu-component">
                <div className="profile-menu-component-icon profile-menu-component-icon-status"></div>
                <div className="profile-menu-component-text">
                  Узнать статус заказа
                </div>
              </Link>
              <Link to="/profile/history/orders" className="profile-menu-component">
                <div className="profile-menu-component-icon profile-menu-component-icon-history"></div>
                <div className="profile-menu-component-text">
                  История заказов
                </div>
              </Link>
              <Link to="/profile/address" className="profile-menu-component">
                <div className="profile-menu-component-icon profile-menu-component-icon-address"></div>
                <div className="profile-menu-component-text">
                  Адреса доставки
                </div>
              </Link>
              <Link to="/profile/password" className="profile-menu-component profile-menu-component-active">
                <div className="profile-menu-component-icon profile-menu-component-icon-password"></div>
                <div className="profile-menu-component-text">
                  Изменить пароль
                </div>
              </Link>
              <Link to="/profile/exit" className="profile-menu-component">
                <div className="profile-menu-component-icon profile-menu-component-icon-exit"></div>
                <div className="profile-menu-component-text">
                  Выйти
                </div>
              </Link>
            </div>
            <div className="profile-content">
              <div className="profile-password-container">
                <div className="profile-password-content-title">
                  Изменить пароль
                </div>
                <div className="profile-password-content-container">
                  <input type="text" className="profile-password-content-element" placeholder='Текущий пароль' />
                  <input type="text" className="profile-password-content-element" placeholder='Новый пароль' />
                  <input type="text" className="profile-password-content-element" placeholder='Повторить пароль' />
                </div>
                <div className="profile-password-content-submit">
                  Обновить пароль
                </div>
                <div className="profile-password-content-privacy">
                  Политика конфиденциальности
                </div>
                <div className="profile-password-content-recommendation">
                  
                </div>
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </React.Fragment>
    )
  }
}