import $ from 'jquery';

export const setSliderEvent = (imagesContainer: JQuery<HTMLElement>, imagesCounter: JQuery<HTMLElement>) => {
  let isDragging: boolean = false;
  let startPosition: any = 0;
  let currentTranslate: any = 0;
  let prevTranslate: any = 0;

  const images: any = imagesContainer.children();
  const imageWidth: any = imagesContainer.width();
  const counterPoints: any = imagesCounter.children();
  const counterPointsActiveClass: string = `${counterPoints.attr('class').split(' ')[0]}-active`;

  for (let i = 0; i < $(images).length; i++) {
    $(images[i]).css({ 'top': `${i * 100 * -1}%`, 'left': `${i * 100}%` });
  };

  const writeCounter = (index: number) => {
    const transformMatrix = imagesContainer.css('transform');
    const matrixValues = transformMatrix.match(/matrix.*\((.+)\)/);
    const translateX = matrixValues ? parseFloat(matrixValues[1].split(', ')[4]) : 0;
    const currentImageIndex: number = Math.round(-translateX / imageWidth);

    counterPoints.removeClass(counterPointsActiveClass);
    $(counterPoints[currentImageIndex]).addClass(counterPointsActiveClass);
  };

  imagesContainer.on('mousedown touchstart', (event) => {
    isDragging = true;
    startPosition = event.type.includes('mouse') ? event.pageX : (event.touches ? event.touches[0].clientX : 0);
    imagesContainer.css('cursor', 'grabbing');
    imagesContainer.css('transition', 'none');
  });

  imagesContainer.on('mousemove touchmove', (event) => {
    if (!isDragging) return;
    const currentPosition: any = event.type.includes('mouse') ? event.pageX : (event.touches && event.touches[0] ? event.touches[0].clientX : 0);
    const diff = currentPosition - startPosition;
    currentTranslate = prevTranslate + diff;
    imagesContainer.css('transform', `translateX(${currentTranslate}px)`);
  });

  const snapToImage = (index: number) => {
    currentTranslate = -index * imageWidth;
    prevTranslate = currentTranslate;

    if (index < 0) {
      currentTranslate = -(images.length - 1) * imageWidth;
      prevTranslate = currentTranslate;
    } else if (index >= images.length) {
      currentTranslate = 0;
      prevTranslate = currentTranslate;
    }

    imagesContainer.css('transition', 'transform 0.3s ease');
    imagesContainer.css('transform', `translateX(${currentTranslate}px)`);
    setTimeout(() => {
      writeCounter(index);
    }, 200);
  };

  imagesContainer.on('mouseup touchend', () => {
    isDragging = false;
    imagesContainer.css('cursor', 'grab');

    const index = Math.round(-currentTranslate / imageWidth);
    snapToImage(index);
  });

  imagesContainer.on('mouseleave', () => {
    if (isDragging) {
      isDragging = false;
      imagesContainer.css('cursor', 'grab');

      const index = Math.round(-currentTranslate / imageWidth);
      snapToImage(index);
    }
  });
};