import React from 'react';
import { Link } from 'react-router-dom';
import { ProfileHeader } from '../ProfileHeader';
import { Footer } from '../Footer';

export class ProfilePersonal_data extends React.Component {
  render() {
    return (
      <React.Fragment>
        <ProfileHeader />
        <div className='profile parentbox'>
          <div className="profile-container childbox">
            <div className="profile-menu">
              <Link to="/profile/status" className="profile-menu-component">
                <div className="profile-menu-component-icon profile-menu-component-icon-status"></div>
                <div className="profile-menu-component-text">
                  Узнать статус заказа
                </div>
              </Link>
              <Link to="/profile/history/orders" className="profile-menu-component">
                <div className="profile-menu-component-icon profile-menu-component-icon-history"></div>
                <div className="profile-menu-component-text">
                  История заказов
                </div>
              </Link>
              <Link to="/profile/address" className="profile-menu-component">
                <div className="profile-menu-component-icon profile-menu-component-icon-address"></div>
                <div className="profile-menu-component-text">
                  Адреса доставки
                </div>
              </Link>
              <Link to="/profile/password" className="profile-menu-component">
                <div className="profile-menu-component-icon profile-menu-component-icon-password"></div>
                <div className="profile-menu-component-text">
                  Изменить пароль
                </div>
              </Link>
              <Link to="/profile/exit" className="profile-menu-component">
                <div className="profile-menu-component-icon profile-menu-component-icon-exit"></div>
                <div className="profile-menu-component-text">
                  Выйти
                </div>
              </Link>
            </div>
            <div className="profile-content">
              <div className="profile-personal_data-container">
                <div className="profile-personal_data-content-fullname-title">
                  Здесь вы можете изменить личные данные
                </div>
                <div className="profile-personal_data-content-fullname-container">
                  <input type="text" className="profile-personal_data-content-fullname-element" placeholder='Фамилия' />
                  <input type="text" className="profile-personal_data-content-fullname-element" placeholder='Имя' />
                  <input type="text" className="profile-personal_data-content-fullname-element" placeholder='Отчество' />
                </div>
                <div className="profile-personal_data-content-birthday-title">
                  Дата рождения
                </div>
                <div className="profile-personal_data-content-birthday-container">
                  <input type="text" className="profile-personal_data-content-birthday-element" placeholder='ДД.ММ.ГГГГ' />
                </div>
                <div className="profile-personal_data-content-contacts-title">
                  Контактные данные
                </div>
                <div className="profile-personal_data-content-contacts-container">
                  <input type="text" className="profile-personal_data-content-contacts-element" placeholder='+7 (9 _ _) _ _ _-__-__' />
                  <input type="text" className="profile-personal_data-content-contacts-element" placeholder='Email' />
                </div>
                <div className="profile-personal_data-content-submit">
                  Обновить информацию
                </div>
                <div className="profile-personal_data-content-privacy">
                  Политика конфиденциальности
                </div>
                <div className="profile-personal_data-content-recomendation">
      
                </div>
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </React.Fragment>
    )
  }
}