import React from 'react';
import { Routes, Route } from 'react-router-dom';
import './index.css'

import { NotFound } from './components/pages/NotFound';
import { Home } from './components/pages/Home';
import { Catalog } from './components/pages/Catalog';
import { CatalogProductPhotos } from './components/pages/CatalogProductPhotos';
import { CatalogProductSets } from './components/pages/CatalogProductSets';
import { CatalogProductSolutions } from './components/pages/CatalogProductSolutions';
import { CatalogFavorites } from './components/pages/CatalogFavorites';
import { ProfilePersonal_data } from './components/pages/Profile_Personal_data';
import { ProfileStatus } from './components/pages/ProfileStatus';
import { ProfileStatusSearch } from './components/pages/ProfileStatusSearch';
import { ProfileHistoryOrders } from './components/pages/ProfileHistoryOrders';
import { ProfileHistoryOrdersProduct } from './components/pages/ProfileHistoryOrdersProduct';
import { ProfileHistoryGoods } from './components/pages/ProfileHistoryGoods';
import { ProfileAddress } from './components/pages/ProfileAddress';
import { ProfilePassword } from './components/pages/ProfilePassword';
import { ProfileExit } from './components/pages/ProfileExit';
import { Cart } from './components/pages/Cart';
import { Delivery } from './components/pages/Delivery';
import { Payment } from './components/pages/Payment';
import { Contacts } from './components/pages/Contacts';
import { About } from './components/pages/About';
import { News } from './components/pages/News';

export class App extends React.Component {
  render() {
    return (
      <React.Fragment>
        <Routes>
          {/* NotFound */}
          <Route path = '*' element = { <NotFound /> } />

          {/* Home */}
          <Route path = '/' element = { <Home /> } />

          {/* Catalog */}
          <Route path = '/catalog' element = { <Catalog /> } />
          <Route path = '/catalog/product/photos' element = { <CatalogProductPhotos /> } />
          <Route path = '/catalog/product/sets' element = { <CatalogProductSets /> } />
          <Route path = '/catalog/product/solutions' element = { <CatalogProductSolutions /> } />
          <Route path = '/catalog/favorites' element = { <CatalogFavorites /> } />

          {/* Profile */}
          <Route path = '/profile/personal_data' element = { <ProfilePersonal_data /> } />
          <Route path = '/profile/status' element = { <ProfileStatus /> } />
          <Route path = '/profile/status/search' element = { <ProfileStatusSearch /> } />
          <Route path = '/profile/history/orders' element = { <ProfileHistoryOrders /> } />
          <Route path = '/profile/history/orders/product' element = { <ProfileHistoryOrdersProduct /> } />
          <Route path = '/profile/history/goods' element = { <ProfileHistoryGoods /> } />
          <Route path = '/profile/address' element = { <ProfileAddress /> } />
          <Route path = '/profile/password' element = { <ProfilePassword /> } />
          <Route path = '/profile/exit' element = { <ProfileExit /> } />

          {/* Cart */}
          <Route path = '/cart' element = { <Cart /> } />

          {/* Delivery */}
          <Route path = '/delivery' element = { <Delivery /> } />

          {/* Payment */}
          <Route path = '/payment' element = { <Payment /> } />

          {/* Contacts */}
          <Route path = '/contacts' element = { <Contacts /> } />

          {/* About */}
          <Route path = '/about' element = { <About /> } />

          {/* News */}
          <Route path = '/news' element = { <News /> } />
        </Routes>
      </React.Fragment>
    )
  }
}