import $ from 'jquery';
import React from 'react';
import { Link } from 'react-router-dom';

export class AddAddress extends React.Component {
  componentDidMount(): void {
    $('.window-background').click(() => {
      $('.window-container').removeClass('visible');
    });
    $('.window-content-close').click(() => {
      $('.window-container').removeClass('visible');
    });
  }
  render() {
    return (
      <React.Fragment>
        <div className="profile-address-add_address-container window-container">
          <div className="profile-address-add_address-background window-background">

          </div>
          <div className="profile-address-add_address-content window-content">
            <div className="profile-address-add_address-content-close window-content-close"></div>
            <div className="profile-address-add_address-content-fields">
              <div className="profile-address-add_address-content-fields-title">
                Добавить адрес
              </div>
              <div className="profile-address-add_address-content-fields-input-container">
                <input id="add_address-city" className="profile-address-add_address-content-fields-input" placeholder='г. Москва, Зеленоград к 336'></input>
                <input id="add_address-apartment" className="profile-address-add_address-content-fields-input" placeholder='Квартира / Офис'></input>
                <input id="add_address-entrance" className="profile-address-add_address-content-fields-input" placeholder='Подъезд'></input>
                <input id="add_address-floor" className="profile-address-add_address-content-fields-input" placeholder='Этаж'></input>
                <input id="add_address-intercom" className="profile-address-add_address-content-fields-input" placeholder='Домофон'></input>
              </div>
              <div className="profile-address-add_address-content-fields-submit">
                Добавить
              </div>
            </div>
            <div className="profile-address-add_address-content-map">
              <div className="profile-address-add_address-content-map-title">
                Уточнить на карте
              </div>
              <div className="profile-address-add_address-content-map-overflow">
                
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    )
  }
}