import React from 'react';
import { Link } from 'react-router-dom';
import { ProfileHeader } from '../ProfileHeader';
import { Footer } from '../Footer';

export class ProfileStatusSearch extends React.Component {
  render() {
    return (
      <React.Fragment>
        <ProfileHeader />
        <div className='profile parentbox'>
          <div className="profile-container childbox">
            <div className="profile-menu">
              <Link to="/profile/status" className="profile-menu-component profile-menu-component-active">
                <div className="profile-menu-component-icon profile-menu-component-icon-status"></div>
                <div className="profile-menu-component-text">
                  Узнать статус заказа
                </div>
              </Link>
              <Link to="/profile/history/orders" className="profile-menu-component">
                <div className="profile-menu-component-icon profile-menu-component-icon-history"></div>
                <div className="profile-menu-component-text">
                  История заказов
                </div>
              </Link>
              <Link to="/profile/address" className="profile-menu-component">
                <div className="profile-menu-component-icon profile-menu-component-icon-address"></div>
                <div className="profile-menu-component-text">
                  Адреса доставки
                </div>
              </Link>
              <Link to="/profile/password" className="profile-menu-component">
                <div className="profile-menu-component-icon profile-menu-component-icon-password"></div>
                <div className="profile-menu-component-text">
                  Изменить пароль
                </div>
              </Link>
              <Link to="/profile/exit" className="profile-menu-component">
                <div className="profile-menu-component-icon profile-menu-component-icon-exit"></div>
                <div className="profile-menu-component-text">
                  Выйти
                </div>
              </Link>
            </div>
            <div className="profile-content">
              <div className="profile-status-search-content-title">
                Поиск заказа
              </div>
              <div className="profile-status-search-content-phone-text">
                Телефон указанный при заказе <span>*</span>
              </div>
              <input type="text" className="profile-status-search-content-phone-field" placeholder='+7 (9 _ _) _ _ _-__-__' />
              <div className="profile-status-search-content-search-container">
                <input type="text" className="profile-status-search-content-search-field" placeholder='Укажите номер заказа' />
                <div className="profile-status-search-content-search-field-icon"></div>
                <div className="profile-status-search-content-search-button">
                  Поиск
                </div>
              </div>
              <div className="profile-status-search-content-results-text">
                РЕЗУЛЬТАТЫ ПОИСКА
              </div>
              <div className="profile-status-search-content-results-container">
                <div className="profile-status-search-content-results-element profile-status-search-content-results-element-prototyping">
                  <div className="profile-status-search-content-results-element-number-text">
                    НОМЕР:
                  </div>
                  <div className="profile-status-search-content-results-element-number">
                    11786
                  </div>
                  <div className="profile-status-search-content-results-element-status-text">
                    СТАТУС:
                  </div>
                  <div className="profile-status-search-content-results-element-status">
                    Макетирование
                  </div>
                  <div className="profile-status-search-content-results-element-price-text">
                    ЦЕНА:
                  </div>
                  <div className="profile-status-search-content-results-element-price">
                    12 500 руб
                  </div>
                  <div className="profile-status-search-content-results-element-chevron">

                  </div>
                </div>
              </div>
              <div className="profile-status-search-content-recommendation">
                  
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </React.Fragment>
    )
  }
}